import Vue from 'vue'
import Router from 'vue-router'
import NewsList from '@/views/NewsList'
import NewsDetail from '@/views/NewsDetail'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  // abstract: true,
  // 定义路由根路径
  // root: '',
  routes: [
    {
      path: '/',
      name: 'NewsList',
      component: NewsList
    },
    {
      path: '/newsDetail',
      name: 'NewsDetail',
      component: NewsDetail
    }
  ]
})



