<template>
	<div class="list_item" @click='go' :id='"listItem" + items.id'>
		<div class="title">{{items.title}}</div>
		<div class="time">发表时间：{{items.created_at}}</div>
		<div class="content">{{items.content | subLength}}</div>
	</div>
</template>
<script>
export default {
  name: 'item',
  props: [ 'items' ],
  created: function () {
    this.eventListener()
  },
  methods: {
    eventListener () {
      this.$nextTick(function () {
        let divEx = document.getElementById('listItem' + this.items.id)
        // console.log(divEx)
        divEx.addEventListener('mouseover', function () {
          divEx.style.background = '#f5f5f5'
          console.log('Over')
        }, false)
        divEx.addEventListener('mouseout', function () {
          divEx.style.background = '#fff'
          console.log('Out')
        }, false)
        divEx.addEventListener('mousedown', function () {
          divEx.style.background = '#f5f5f5'
          console.log('down')
        }, false)
      })
    },
    go () {
      this.$router.push({path: '/newsDetail', query: {id: this.items.id}})
      // console.log(this.items.id)
    }
  },
  filters: {
    subLength (val) {
      if (val.length > 40) {
        val = val.substring(0, 40) + '......'
      }
      return val
    }
  }
}
</script>
<style scoped>
.list_item{
	padding: 5%; 
	border-bottom: 1px #dadada solid;
}
.title{

}
.time{
	color: #d4d4d4;
    font-size: 5px;
}
.content{
	
}
</style>


