<template>
	<div class='NewsList'>
		<listItem v-for='item in newList' :items='item' :key='item.id'></listItem>
	</div>
</template>
<script>
import listItem from '../components/ListItem'
export default ({
  name: 'NewsList',
  data () {
    return {
      newList: []
    }
  },
  components: {
    listItem
  },
  created: function () {
    this.iniData()
  },
  methods: {
    iniData () {
      let that = this
      // this.$http.get('http://localhost/laravelVue/public/api/posts').then(function (response) {
      this.$http.get('http://laravelvue.dev/api/posts').then(function (response) {
        that.newList = response['data']
        // console.log(response)
      }).catch(function (error) {
        console.log(error)
      })
    }
  }
})
</script>


