<template>
	<div class="detail">
		<div class="title">{{news.title}}</div>
		<div class="time">{{news.created_at}}</div>
		<div class="content" v-html="news.content"></div>
	</div>
</template>
<script>
export default ({
  name: 'detail',
  data () {
    return {
      news: {},
      id: 0
    }
  },
  created: function () {
    this.id = this.$router.currentRoute.query.id
    console.log(this.id + 'qq')
    this.iniData()
  },
  methods: {
    iniData () {
      let that = this
      // this.$http.get('http://localhost/laravelVue/public/api/postDetail/' + this.id).then(function (response) {
      this.$http.get('http://laravelvue.dev/api/postDetail/' + this.id).then(function (response) {
        that.news = response['data']
        // console.log(response)
      }).catch(function (error) {
        console.log(error)
      })
    }
  }
})
</script>
<style scoped>
.detail{
	padding: 5%;
}
.title{
	text-align: center;
	font-size: 20px;
	font-weight: 700;
}
.time{
	text-align: right;
    font-size: 12px;
    color: #b5b5b5;
    margin: 5% 0;
}
.content{
	
}
</style>


